/* Глобальный фон с анимацией */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background: linear-gradient(135deg, #2f2f2f, #434343, #2f4f6e);
    background-size: 400% 400%; /* Настройка для анимации */
    animation: gradientAnimation 8s ease infinite; /* Плавная анимация */
    color: #eaeaea;
  }
  
  /* Анимация градиента */
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  /* Основной контейнер */
  .main-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.8); /* Полупрозрачный фон контейнера */
    border-radius: 12px;
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.25);
  }
  
  /* Эффект для элементов меню */
  .menu-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    width: 100%;
    max-width: 1200px;
  }
  
  .menu-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: rgba(40, 40, 40, 0.9);
    border: 1px solid #555;
    border-radius: 10px;
    padding: 15px;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
  }
  
  .menu-item:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Подсветка при наведении */
    transform: scale(1.1);
  }
  
  /* Кнопка с эффектом */
  .menu-button {
    background: linear-gradient(45deg, #2f4f6e, #434343);
    color: #eaeaea;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    font-weight: bold;
    text-shadow: 0px 1px 2px black;
    transition: background 0.3s ease, transform 0.2s ease;
  }
  
  .menu-button:hover {
    background: linear-gradient(45deg, #434343, #2f4f6e);
    transform: scale(1.05);
  }
  
  /* Заголовок */
  .menu-title {
    font-size: 2rem;
    color: #eaeaea;
    text-shadow: 0px 2px 4px black;
    margin-bottom: 20px;
  }
  
  /* Адаптивность */
  @media (max-width: 768px) {
    .menu-grid {
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
  
    .menu-title {
      font-size: 1.8rem;
    }
  
    .menu-button {
      padding: 8px 15px;
      font-size: 0.95rem;
    }
  }
  
  @media (max-width: 480px) {
    .menu-grid {
      grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    }
  
    .menu-item {
      padding: 10px;
    }
  
    .menu-button {
      padding: 8px 12px;
      font-size: 0.85rem;
    }
  }
  