.container {
  padding: 20px;
  text-align: center;
}

h2 {
  margin-bottom: 20px;
  color: #2f4f6e;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding: 15px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.card-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 10px;
}

.card-title {
  font-size: 1.2rem;
  margin: 10px 0;
  color: #333;
}

.card-description {
  font-size: 0.9rem;
  color: #555;
}

.back-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #2f4f6e;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #0056b3;
}
