.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #2f4f6e;
  padding: 20px;
  color: white;
}

.header-title {
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.search-container {
  position: relative;
  width: 100%;
  max-width: 400px;
}

.search-input {
  width: 80%; /* Измените ширину на нужный процент или значение */
  max-width: 600px; /* Укажите максимальную ширину поля */
  height: 50px; /* Установите высоту поля */
  padding: 10px; /* Отступ внутри поля */
  font-size: 1.2rem; /* Увеличьте размер шрифта */
  border: 1px solid #ccc;
  border-radius: 8px; /* Округленные углы */
  outline: none;
}


.suggestions-list {
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  max-height: 150px;
  overflow-y: auto;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.suggestion-item {
  padding: 10px;
  font-size: 1rem;
  color: #333;
  background-color: #f9f9f9;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.suggestion-item:hover {
  background-color: #e0f7fa;
  color: #00796b;
}
